@font-face {
  font-family: "GeeksBet";
  src: url("../../../assets/font/GEEKS\ Bet\ \(Normal\).ttf") format("truetype");
}
@font-face {
  font-family: "GeeksWin95";
  src: url("../../../assets/font/GEEKS\ OzHandicraft\ Win95BT\ \(Roman\).ttf")
    format("truetype");
}

.container {
  cursor: pointer;
  transition: 0.3s;
}

.bottomContainer {
  height: 85px;
}

.titleContainer {
  display: flex;
  justify-content: center;
  border-radius: 10px;
}

.category {
  background-color: #2c3d51;
  color: #fff;
  border-bottom-left-radius: 10px;
  border-top-left-radius: 10px;
  font-size: 14px;
  padding: 4px 10px;
  font-family: GeeksWin95;
}

.price {
  background-color: #2c3d51;
  color: #fff;
  margin-left: 2px;
  border-bottom-right-radius: 10px;
  border-top-right-radius: 10px;
  font-size: 14px;
  padding: 4px 12px;
  font-family: GeeksBet;
}

.name {
  text-align: center;
  margin: 7px 0;
  font-size: 20px;
  font-family: GeeksBet;
}

.container:hover {
  -webkit-box-shadow: 10px 10px 20px 0px rgba(0, 0, 0, 0.5);
  -moz-box-shadow: 10px 10px 20px 0px rgba(0, 0, 0, 0.5);
  box-shadow: 10px 10px 20px 0px rgba(0, 0, 0, 0.5);
}

.container:hover .price {
  background-color: #82d900;
}

.container:hover .category {
  background-color: #82d900;
}

.header {
  display: flex;
  min-width: 300px;
  width: 33.2%;
  min-height: 60px;
  height: 10vh;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: 0.2s;
}

.header:hover {
  -webkit-box-shadow: 10px 10px 20px -10px rgba(0, 0, 0, 0.7);
  -moz-box-shadow: 10px 10px 20px -10px rgba(0, 0, 0, 0.7);
  box-shadow: 10px 10px 20px -10px rgba(0, 0, 0, 0.7);
  transform: scale(1.05);
}
